
@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

body{
  
  background-image: url('./images/candycrushsaga.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Raleway',sans-serif;
  overflow: hidden;
} 
.App{
  display: flex;
  
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:30px;
  
  
}
.game{
  background-color: #74EBD5;
background-image: linear-gradient(90deg, #74EBD5 0%, #9FACE6 100%);

  margin: 0 auto;
  width:400px;
  height:400px;
  display:flex;
  padding:10px;
  flex-wrap:wrap;
  box-shadow: 0 0 20px rgba(0,0,0,0.7);
  border-radius: 1rem;
}
.game img{
  width:50px;
  height:50px;
}
.image{
  width: 140px;
}
.score-board{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:1rem;
  width:150px;
  height: 60px;
  border-radius: 2rem;
  background-color: #21D4FD;
  color: rgb(20, 20, 34);
background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);



  

}
.heading{
  
  color:#08367c;
  text-decoration: underline;
  font-size: 2.5rem;
}

@media screen and (max-width:400px) {
  .game img{
    width:35px;
    height:35px;
  }
  .game{
    width:280px;
    height:280px;
  }
}
